@import url(https://fonts.googleapis.com/css2?family=Potta+One&display=swap);
/** @format */
.App {
  margin: 0;
  padding: 0;
  text-align: center;
  color: #204a4d;
  overflow: scroll;
  scroll-behavior: smooth;
  font-family: "Potta One", cursive;
  background-color: #00bdde;
  background-image: url("https://www.transparenttextures.com/patterns/brick-wall-dark.png");
  /* This is mostly intended for prototyping; please download the pattern and re-host for production environments. Thank you! */
}
.intro {
  display: grid;
  grid-template-columns: auto auto;
}
.avatarIntro {
  width: 700px;
}
h1 {
  width: 70%;
  margin-top: 200px;
  font-size: 6vw;
  color: peachpuff;
}
.intro-p {
  color: peachpuff;
  font-size: 30px;
}

@media (max-width: 768px) {
  body {
    width: 100%;
    display: grid;
    grid-column: auto;
  }
  .intro {
    margin: 10px;
    display: grid;
    grid-template-columns: auto;
  }
  .avatarIntro {
    width: 400px;
  }
  h1 {
    font-size: 8vh;
    position: relative;
    bottom: 200px;
  }
  .intro-p {
    font-size: 6vh;
    position: relative;
    bottom: 100px;
  }
}

/** @format */

h2 {
  font-size: 6vw;
}

.bootcamp {
  background-image: url("https://cdn.wallpapersafari.com/8/71/Mapn1v.gif");
  background-repeat: no-repeat;
  background-size: 100%;
  height: 160vh;
}

.bootcamp-div {
  padding: 20px;
}
.bootcampAnimation {
  position: relative;
  top: 180px;
}
.p {
  width: 35%;
  position: relative;
  bottom: 220px;
}
#itbootcamp-logo {
  width: 320px;
  height: 100px;
  position: relative;
  bottom: 490px;
  right: 10px;
  margin-left: 5px;
  animation-name: bootcampAnimation;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-delay: 1s;
}
#git-ico {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: solid 4px #ff9803;
  position: relative;
  bottom: 420px;
  left: 20px;
  animation-name: gitAnimation;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-delay: 3s;
  animation-direction: reverse;
}

#js-ico {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: solid 4px #ff9803;
  position: relative;
  bottom: 380px;
  left: 80px;
  animation-name: jsAnimation;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-delay: 3s;
  animation-direction: reverse;
}
#react-ico {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: solid 4px #ff9803;
  position: relative;
  bottom: 300px;
  left: 120px;
  animation-name: reactAnimation;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-delay: 3s;
  animation-direction: reverse;
}
#school-woman {
  position: relative;
  right: 70px;
  top: 230px;
  animation-name: schoolWomanAnimation;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-delay: 3s;
}
@keyframes gitAnimation {
  0% {
    top: -420px;
    left: 20px;
  }
  25% {
    top: -380px;
    left: 100px;
    transform: rotate(360deg);
  }
  50% {
    top: -320px;
    left: 200px;
    transform: rotate(360deg);
  }
  75% {
    top: -250px;
    left: 330px;
    transform: rotate(360deg);
  }
  100% {
    top: -420px;
    left: 20px;
    visibility: hidden;
  }
}
@keyframes jsAnimation {
  0% {
    top: -380px;
    left: 80px;
  }
  25% {
    top: -330px;
    left: 120px;
    transform: rotate(360deg);
  }
  50% {
    top: -300px;
    left: 180px;
    transform: rotate(360deg);
  }
  75% {
    top: -250px;
    left: 250px;
    transform: rotate(360deg);
  }
  100% {
    top: -380px;
    left: 80px;
    visibility: hidden;
  }
}
@keyframes reactAnimation {
  0% {
    top: -300px;
    left: 120px;
  }
  25% {
    top: -280px;
    left: 140px;
    transform: rotate(360deg);
  }
  50% {
    top: -250px;
    left: 150px;
    transform: rotate(360deg);
  }
  75% {
    top: -200px;
    left: 150px;
    transform: rotate(360deg);
  }
  100% {
    top: -300px;
    left: 120px;
    visibility: hidden;
  }
}
@keyframes schoolWomanAnimation {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.2);
  }
  50% {
    transform: scale(1.3);
  }
  75% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes bootcampAnimation {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.2);
  }
  50% {
    transform: scale(1.3);
  }
  75% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
@media (max-width: 768px) {
