    height: 50px;
    position: relative;
    bottom: 390px;
    right: 0px;
  }
  #git-ico {
    width: 50px;
    height: 50px;
    bottom: 350px;
    left: 10px;
  }
  #js-ico {
    width: 50px;
    height: 50px;
    bottom: 300px;
    left: 10px;
  }
  #react-ico {
    width: 50px;
    height: 50px;
    bottom: 250px;
    left: 10px;
  }
  #school-woman {
    width: 200px;
    position: relative;
    top: 0px;
    right: 0px;
  }
  .p {
