  width: 100%;
  height: 120vh;
}
.screenOne-p {
  background-color: peachpuff;
}
#phone {
  position: relative;
  left: 120px;
  top: 140px;
}
#accepted {
  width: 200px;
  position: relative;
  left: 180px;
  bottom: 100px;
  visibility: hidden;
  animation-name: acceptedAnimation;
  animation-duration: 30s;
  animation-iteration-count: infinite;
  animation-delay: 5s;
}
@keyframes acceptedAnimation {
  0% {
    visibility: hidden;
  }
  25% {
    visibility: hidden;
  }
  50% {
    visibility: hidden;
  }
  75% {
    visibility: visible;
  }
  100% {
    visibility: visible;
    transform: scale(1.3);
  }
}
#coursera {
  width: 200px;
  position: relative;
  left: 20px;
  bottom: 50px;
  visibility: hidden;
  animation-name: courseraAnimation;
  animation-duration: 30s;
  animation-iteration-count: infinite;
  animation-delay: 5s;
}
@keyframes courseraAnimation {
  0% {
    visibility: hidden;
  }
  25% {
    visibility: visible;
  }
  50% {
    visibility: visible;
    transform: scale(1.3);
  }
  75% {
    visibility: hidden;
  }
  100% {
    visibility: hidden;
  }
}
#html {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  position: relative;
  bottom: 80px;
  right: 80px;
  animation-name: htmlAnimation;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-delay: 4s;
}
@keyframes htmlAnimation {
  0% {
    visibility: hidden;
  }
  25% {
    visibility: visible;
    bottom: 80px;
    right: 80px;
    transform: rotate(360deg);
  }
  50% {
    visibility: visible;
    bottom: 120px;
    left: 100px;
    transform: rotate(360deg);
  }
  75% {
    visibility: visible;
    bottom: 120px;
    left: 220px;
    transform: rotate(360deg);
  }
  100% {
    visibility: hidden;
  }
}
#css {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  position: relative;
  bottom: 80px;
  right: 50px;
  animation-name: cssAnimation;
  animation-duration: 10s;
