}
@keyframes cssAnimation {
  0% {
    visibility: hidden;
  }
  25% {
    visibility: visible;
    bottom: 80px;
    right: 50px;
    transform: rotate(360deg);
  }
  50% {
    visibility: visible;
    bottom: 120px;
    left: 100px;
    transform: rotate(360deg);
  }
  75% {
    visibility: visible;
    bottom: 120px;
    left: 220px;
    transform: rotate(360deg);
  }
  100% {
